import React from 'react';
import Helmet from 'react-helmet';

import Layout from 'components/Layout';
import SEO from 'components/SEO';
import Title from 'components/Title';

const Stack = () => (
    <Layout>
        <SEO 
            pageTitle="Stack"
            description = "Student, Front-end developer"
            keywords = "React, Node, Gastby, Ionic, Front-End"
        ></SEO>
        <Helmet>
            <script 
                async 
                src="https://cdn1.stackshare.io/javascripts/client-code.js" charset="utf-8"
            />
        </Helmet>
        <section className="section content is-size-4-desktop is-size-5-touch">
            <Title>Stack</Title>
            <div className="has-text-centered">
                <p className="content">
                These modern tools I use in my projects
                </p>
                <p className="content">Here is my stack on StackShare</p>
                <div className="columns is-centered">
                    <div className="column is-half ">
                        <a 
                            frameBorder="0" 
                            data-theme="dark" 
                            data-layers="1,2,3,4" 
                            data-stack-embed="true" 
                            href="https://embed.stackshare.io/stacks/embed/52d052caaa3703dda2518364b3c28b" 
                            
                            /> 
                    </div>
                </div>
            </div>
        </section>
    </Layout>
);

export default Stack;